<template>
  <div>
    <div
      class="
        container
        bg-clip-padding
        backdrop-filter backdrop-blur-xl
        bg-opacity-30
        border border-gray-200
        bg-gray-400
        w-full
        md:w-1/2
        mx-auto
        p-5
        rounded
        shadow-sm
      "
    >
      <h2 class="mx-auto text-center text-2xl mb-3 font-semibold text-white">
        Identify Yourself
      </h2>
      <form ref="form" class="space-y-3">
        <div class="my-2">
          <p class="text-center kalpurush text-lg text-white">
            টাকা পাঠানোর সময় যে ইমেইল ব্যবহার করেছিলে এইখানেও একই ইমেইল ব্যবহার
            করে প্রবেশ করবে।
          </p>
        </div>
        <div>
          <label class="label-style text-white">E-mail</label>
          <input
            required
            type="email"
            class="input-block"
            v-model="cred"
            placeholder="eg. example@example.com"
          />
        </div>

        <div
          class="mt-4 bg-red-600 text-white rounded px-2 py-3"
          v-if="message"
        >
          {{ message }}
        </div>

        <div class="text-center">
          <button
            type="button"
            @click="getClusterInfo"
            class="
              px-5
              py-3
              bg-gradient-to-r
              from-red-400
              to-red-600
              font-semibold
              text-white
              rounded
              flex
              mx-auto
              hover:border-red-900
              border
            "
          >
            <div
              v-if="loading"
              class="
                w-6
                h-6
                mx-1
                border-4 border-t-4 border-white
                rounded-full
                animate-spin
                loader_btn
              "
            />
            {{ loading ? "Loading..." : "Submit" }}
          </button>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      loading: false,

      cred: "",

      message: "",
    };
  },
  methods: {
    getClusterInfo() {
      if (this.$refs.form.checkValidity()) {
        this.loading = !this.loading;
        fetch(
          "https://script.google.com/macros/s/AKfycbzLogP9mOma_piWaviR450VDk3v7D6Ui-nwg1nCjXKBeKYDLsGfWdPNZ5DThMx9ciHG/exec?email=" +
            this.cred
        )
          .then((res) => res.json())
          .then((data) => {
            if (data.found) {
              this.$store.dispatch("userLogin", data);
              this.$router.push("/");
              localStorage.setItem("acuser", this.cred);
              this.loading = !this.loading;
            } else {
              this.loading = false;
              this.message = "You don't purchased any cluster";
            }
          });
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.numberbg {
  background: rgba(255, 255, 255, 0.7) !important;
  border-radius: 35px;
}
.loader_btn {
  border-top-color: rgb(77, 75, 75) !important;
}
</style>